.textInput {
  border-radius: 15px;
  padding: 5px 12px 5px 12px;
  width: 70%;
  max-width: 70%;
  min-width: 0;
  border: 0;
  resize: none;
  overflow: scroll;
  max-height: 5rem;
  min-height: 3rem;
  vertical-align: bottom;
  font-size: large;
  margin-left: 2px;
  background-color: #E0E0E0;
  color: black;
  font-family: Verdana, sans-serif;
}

::placeholder{
color: #696969;
}

.submitButton {
  border-radius: 25px;
  padding: 5px 2px 5px 2px;
  width: 5rem;
  background-color: #2D68C4;
  color: white;
  border: 0;
  max-width: 7rem;
  font-size: large;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  vertical-align: initial;
  margin: 0;
  right: 0;
}

form {
max-width: 100%;
flex-direction: row;
align-items: flex-end;
position: relative;
display: flex;
box-sizing: border-box;
justify-content: space-around;
}
